// @ts-check

/**
 * @typedef {import("mithril").Vnode} Vnode
 * @typedef {import("../index")}
 * @typedef {import("../index").ComponentCreatorOptions} ComponentCreatorOptions
 */

import isEqual from "lodash.isequal";
import stream from "mithril/stream";
import { renderer } from "./renderer";
import { keys } from "./keys";

const requiresKeys = false;

/**
 * @param {ComponentCreatorOptions} params
 */
export const ComponentCreator = ({
  createContent = () => null,
  createProps = () => ({}),
  component = null,
  getElement = () => "div",
  getInitialState = () => ({}),
  onMount = () => null,
  onUnMount = () => null,
  onUpdate = () => null,
  view = null,
}) => {

  const localState = {
    mounted: false,
    value: null,
  };

  /**
   * @param {Vnode} vnode 
   */
  const oninit = vnode => {
    /**
     * @type {{ redrawOnUpdate?: Array<function>, _?: any }} initialState
     */
    const initialState = getInitialState(vnode, stream, { keys });
    Object.assign(vnode.state, initialState);

    initialState.redrawOnUpdate !== undefined
      ? initialState.redrawOnUpdate.map(value => {
        if (localState.mounted) {
          if (!isEqual(value, localState.value)) {
            new Promise(resolve => requestAnimationFrame(resolve)).then(renderer.redraw)
            localState.value = value;
          }
        }
      })
      : undefined;
  };

  /**
   * @param {Vnode} vnode 
   */
  const oncreate = vnode => {
    localState.mounted = true;
    onMount(vnode, { keys });
  };

  /**
   * @param {Vnode} vnode 
   */
  const render = vnode => {
    return renderer(
      component || getElement(vnode),
      createProps(vnode, { renderer, requiresKeys, keys }),
      [
        vnode.attrs.before,
        createContent(vnode, { renderer, requiresKeys, keys }),
        vnode.attrs.after
      ]
    );
  };

  return {
    view: view
      ?
      /**
       * @param {Vnode} vnode
       */
      vnode => view(vnode, { render, renderer })
      :
      /**
       * @param {Vnode} vnode
       */
      vnode => render(vnode),
    oninit,
    oncreate,
    onremove: onUnMount,
    onupdate: onUpdate,
  };
};
